import { Component, OnInit, OnDestroy } from '@angular/core';

import { Subscription } from 'rxjs';
import { map } from 'rxjs/operators';
import { ActivatedRoute } from '@angular/router';
import { NewsRubric } from 'src/app/services/news-rubrics/news-rubric';
import { CompaniesService } from 'src/app/services/companies/companies.service';

@Component({
  selector: 'app-enterprise-page',
  templateUrl: './enterprise-page.component.html',
  styleUrls: ['./enterprise-page.component.scss']
})
export class EnterprisePageComponent implements OnInit, OnDestroy {
  public webId: string = null;
  public eventRubricId: number = null;
  public newsRubricId: number = null;
  public companyId: number = null;
  private subscribeCompanyId: Subscription = null;
  private subscriptionWebId: Subscription = null;
  private appContainerStyle = { 'background-color': '#f7f7f7' };
  private appShowSidebar: boolean = true;

  constructor(private route: ActivatedRoute,
    private companiesService: CompaniesService) {}

  ngOnInit() {
    this.getNewsRubric();
    this.subscribe();
  }

  ngOnDestroy() {
    this.unSubscribe();
  }

  private subscribe() {
    this.subscribeCompanyId = this.route.paramMap.subscribe(params => {
      this.companyId = Number(params.get('id'));
      this.companiesService.getItem({ id: this.companyId }).subscribe(c => {
        this.companiesService.setCurrentCompany(c);
      });
    });
    this.subscriptionWebId = this.route.data
      .pipe(map(data => data.webId))
      .subscribe(webId => {
        this.webId = webId;
      });
  }

  private unSubscribe() {
    if (this.subscribeCompanyId) {
      this.subscribeCompanyId.unsubscribe();
    }
    if (this.subscriptionWebId) {
      this.subscriptionWebId.unsubscribe();
    }
  }

  private getNewsRubric() {
    const rubrics: NewsRubric[] = this.route.snapshot.data.newsRubrics;
    const eventRubric: NewsRubric[] = rubrics.filter(
      r => r.title === 'Событие'
    );
    if (eventRubric.length > 0) {
      this.eventRubricId = eventRubric[0].id;
    }
    const newsRubric: NewsRubric[] = rubrics.filter(
      r => r.title === 'Новость предприятия'
    );
    if (newsRubric.length > 0) {
      this.newsRubricId = newsRubric[0].id;
    }
  }
}
