import { Component, OnInit, NgZone } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { map } from 'rxjs/operators';

import { Company } from 'src/app/services/companies/company';
import { Banner } from 'src/app/services/banners/banner';
import { SharepointService } from 'src/app/services/sharepoint/sharepoint.service';
import { BannersService } from 'src/app/services/banners/banners.service';

@Component({
  selector: 'app-top-carousel',
  templateUrl: './top-carousel.component.html',
  styleUrls: ['./top-carousel.component.scss']
})
export class TopCarouselComponent implements OnInit {

  public carouselIsUp: Boolean = false;
  public currentSlide = 0;
  public webId: string = null;
  public company: Company = null;
  private subscription: Subscription = null;
  public loading: Boolean = true;

  public banners: Banner[] = [];

  constructor(private route: ActivatedRoute,
    private sharepointService: SharepointService,
    private bannersService: BannersService,
    private zone: NgZone,
    private router: Router) { }

  ngOnInit() {
    this.subscription = this.route.data
      .pipe(map(data => data.webId))
      .subscribe(webId => {
        this.webId = webId;
        this.load();
      });
    this.subscription = this.route.data
      .pipe(map(data => data.company))
      .subscribe(company => {
        this.company = company;
      });
  }

  private load() {
    this.bannersService.getItems({
      filter: 'slShow eq 1 and '
        + `(slNewDate eq null or slNewDate le datetime'${new Date().toISOString()}') and `
        + `(slEndDate eq null or slEndDate ge datetime'${new Date().toISOString()}')`,
        webId: this.webId })
      .subscribe(banners => {
        this.currentSlide = 0;
        this.banners = banners;
        this.loading = false;
      });
  }

  public carouselToFront(val) {
    this.carouselIsUp = val;
  }

  create() {
    window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + `${this.company.url}/Lists/slBanners/NewForm.aspx?Source=`
      + encodeURIComponent(window.location.href);
  }

  edit(item: any) {
    window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + `${this.company.url}/Lists/slBanners/EditForm.aspx?ID=${item.id}&Source=`
      + encodeURIComponent(window.location.href);
  }

  remove(item: any) {
    this.bannersService.recycleItem(item, this.webId)
      .subscribe(() => this.load());
  }

  public prepareLink(link: string) {
    return link && link.replace(/\(/gi, '\\(').replace(/\)/gi, '\\)').replace(/'/gi, '%27').replace(/ /gi, '%20')  || null;
  }

  public goTo(item: Banner) {
    this.zone.run(() => {
      const link = item.url;
      if (window.location.href.indexOf('/PortalPages/') > 0
        && link.indexOf('/PortalPages/') >= 0
        && link.indexOf('#') >= 0) {
        this.router.navigate([link.split('#')[1]]);
      } else {
        window.location.href = link;
      }
    });
  }
}
