import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { EnterprisePageComponent } from './components/enterprise-page/enterprise-page.component';
import { GroupsResolver } from 'src/app/services/security/groups.resolver';
import { CompanyWebResolver } from 'src/app/services/companies/company-web.resolver';
import { CompanyResolver } from 'src/app/services/companies/company.resolver';
import { NewsRubricsResolver } from 'src/app/services/news-rubrics/news-rubrics.resolver';
import { IncidentsInformerResolver } from '../services/incidents/incidents-informer.resolver';

const routes: Routes = [
  {
    path: ':id',
    component: EnterprisePageComponent,
    resolve: {
      groups: GroupsResolver,
      webId: CompanyWebResolver,
      company: CompanyResolver,
      incident: IncidentsInformerResolver,
      newsRubrics: NewsRubricsResolver
    },
    data: { animation: 'fadeAnimation', title: 'Предприятие' }
  },
  {
    path: 'news',
    loadChildren: '../news/news.module#NewsModule'
  },
  {
    path: 'birthdays',
    loadChildren: '../birthdays/birthdays.module#BirthdaysModule'
  },
  {
    path: ':id/media/:webId',
    loadChildren: '../mediagallery/mediagallery.module#MediagalleryModule'
  },
  {
    path: 'booking',
    loadChildren: '../booking/booking.module#BookingModule'
  },
  {
    path: 'incidents',
    loadChildren: '../incidents/incidents.module#IncidentsModule'
  },
  {
    path: 'requests',
    loadChildren: '../requests/requests.module#RequestsModule'
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  providers: [
    GroupsResolver,
    CompanyWebResolver,
    CompanyResolver,
    IncidentsInformerResolver,
    NewsRubricsResolver
  ],
  exports: [RouterModule]
})
export class EnterpriseRoutingModule {}
