import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CookieService } from 'ngx-cookie-service';
import { flatMap, map } from 'rxjs/operators';
import { of, Subscription } from 'rxjs';

import { contentTypes } from 'src/app/constants/content-types';
import { Company } from 'src/app/services/companies/company';
import { SharepointService } from 'src/app/services/sharepoint/sharepoint.service';
import { PhotoLibraryService } from 'src/app/services/photo-library/photo-library.service';
import { VideosService } from 'src/app/services/videos/videos.service';
import { ImagesService } from 'src/app/services/images/images.service';
import { PhotoLibrary } from 'src/app/services/photo-library/photo-library';
import { Video } from 'src/app/services/videos/video';

@Component({
  selector: 'app-enterprise-media-carousel',
  templateUrl: './enterprise-media-carousel.component.html',
  styleUrls: ['./enterprise-media-carousel.component.scss']
})
export class EnterpriseMediaCarouselComponent implements OnInit {

  public webId: string = null;
  public company: Company = null;
  private subscription: Subscription = null;
  public albums: any[] = [];
  public carouselIsUp: Boolean = false;
  public currentSlide = 0;
  public albumIndex = 0;
  public loading: Boolean = true;

  constructor(private route: ActivatedRoute,
    private sharepointService: SharepointService,
    private photoLibraryService: PhotoLibraryService,
    private videosService: VideosService,
    private imagesService: ImagesService,
    private cookieService: CookieService) { }

  ngOnInit() {
    this.subscription = this.route.data
      .pipe(map(data => data.webId))
      .subscribe(webId => {
        this.webId = webId;
        this.load();
      });
    this.subscription = this.route.data
      .pipe(map(data => data.company))
      .subscribe(company => {
        this.company = company;
      });
  }

  load() {
    this.photoLibraryService.getItems({filter: `ContentType eq '${encodeURI(contentTypes.album)}'`, top: 1, webId: this.webId}).pipe(
      flatMap((album: PhotoLibrary[]) => {
        this.albumIndex = album.length > 0 ? album[0].id : 0;
        return album.length > 0 ? this.photoLibraryService.getFiles(album[0].id, this.webId) : of([]);
      })
    ).subscribe({
      next: (value: any) => {
        for (let i = 0; i < value.length; i++) {
          this.albums.push(value[i]);
        }
      },
      complete: () => {
        this.videosService.getRandomItems({top: 10, webId: this.webId}).pipe(
          flatMap((album: Video[]) => {
            return of(album);
          })
          ).subscribe({
          next: (value: any) => {
            for (let i = 0; i < value.length; i++) {
              this.albums.push(value[i]);
            }
          },
          complete: () => {
            this.albums.sort(() => 0.5 - Math.random());
            this.albums.length = 10;
          }
        });
      }
    });
    this.loading = false;
  }

  create(currentSlide?: any) {
    this.cookieService.set('splnu', '0', 1, '/');
    if (currentSlide && currentSlide.videoUrl !== undefined) {
      window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + `${this.company.url}/Lists/slVideos/NewForm.aspx?Source=`
      + encodeURIComponent(window.location.href);
    } else if (currentSlide && currentSlide.url !== undefined) {
      window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + `${this.company.url}/slPhotoLibrary/Forms/Upload.aspx?ContentTypeId=0x012000BFB863A2672645ABBEA198EF8334172D&Source=`
      + encodeURIComponent(window.location.href);
    }
  }

  edit(currentSlide?: any) {
    if (currentSlide && currentSlide.videoUrl !== undefined) {
      window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + `${this.company.url}/Lists/slVideos/AllItems.aspx`;
    } else if (currentSlide && currentSlide.url !== undefined) {
      window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + `${this.company.url}/slPhotoLibrary/Forms/Thumbnails.aspx#/company`;
    }
  }

  public carouselToFront(val) {
    this.carouselIsUp = val;
  }

  public prepareLink(link: string) {
    return '"' + this.imagesService.prepareBackgroundUrl(link) + '"';
  }

  public getFileNameAndExtension(path) {
    const str = path
      .split('\\')
      .pop()
      .split('/')
      .pop();
    const ext = str.substr((Math.max(0, str.lastIndexOf('.')) || Infinity) + 1);
    const name = str.replace('.' + ext, '');
    return {
      ext,
      name,
      full: `${name}.${ext}`
    };
  }
}
