import { Component, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { ActivatedRoute } from '@angular/router';
import { map } from 'rxjs/operators';

import { Volunteering } from 'src/app/services/volunteering/volunteering';
import { Company } from 'src/app/services/companies/company';
import { SharepointService } from 'src/app/services/sharepoint/sharepoint.service';
import { VolunteeringService } from 'src/app/services/volunteering/volunteering.service';

@Component({
  selector: 'app-volunteering-informer',
  templateUrl: './volunteering-informer.component.html',
  styleUrls: ['./volunteering-informer.component.scss']
})
export class VolunteeringInformerComponent implements OnInit {

  public volunteering: Volunteering[] = null;

  public carouselIsUp: Boolean = false;
  public currentSlide = 0;
  private webId: string = null;
  private company: Company = null;
  private subscription: Subscription = null;
  public loading: Boolean = true;

  constructor(private route: ActivatedRoute,
    private sharepointService: SharepointService,
    private volunteeringService: VolunteeringService) { }

  ngOnInit() {
    this.subscription = this.route.data
      .pipe(map(data => data.webId))
      .subscribe(webId => {
        this.webId = webId;
        this.load();
      });
    this.subscription = this.route.data
      .pipe(map(data => data.company))
      .subscribe(company => {
        this.company = company;
      });
  }

  private load() {
    this.volunteeringService.getItems({ top: 3, orderBy: [['Created', false]], webId: this.webId})
      .subscribe((volunteering: Volunteering[]) => this.volunteering = volunteering);
      this.loading = false;
  }

  public carouselToFront(val) {
    this.carouselIsUp = val;
  }

  create() {
    window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + `${this.company.url}/Lists/slVolunteering/NewForm.aspx?Source=`
      + encodeURIComponent(window.location.href);
  }

  edit(item: any) {
    window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + `${this.company.url}/Lists/slVolunteering/EditForm.aspx?ID=${item.id}&Source=`
      + encodeURIComponent(window.location.href);
  }

  remove(item: any) {
    this.volunteeringService.recycleItem(item, this.webId)
      .subscribe(() => this.load());
  }

  public prepareLink(link: string) {
    return link && link.replace(/\(/gi, '\\(').replace(/\)/gi, '\\)').replace(/'/gi, '%27').replace(/ /gi, '%20')  || null;
  }

}
