import { Component, OnInit, Inject, OnDestroy } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs';
import { map } from 'rxjs/operators';

import { Company } from 'src/app/services/companies/company';
import { Employee } from 'src/app/staff/services/employees/employee';
import { EmployeesFactory } from 'src/app/staff/services/employees/employees.factory';

@Component({
  selector: 'app-enterprise-birthdays-informer',
  templateUrl: './enterprise-birthdays-informer.component.html',
  styleUrls: ['./enterprise-birthdays-informer.component.scss']
})
export class EnterpriseBirthdaysInformerComponent implements OnInit, OnDestroy {
  public employees: Employee[] = [];
  public currentDay: number;
  public company: Company = null;
  public threeDays = false;
  public loading: Boolean = true;

  private subscribeEmployees: Subscription = null;

  constructor(
    private route: ActivatedRoute,
    @Inject('EmployeesFactory') private employeesFactory: EmployeesFactory
  ) {
    this.currentDay = new Date().getDate();
  }

  public getArray(i: number) {
    return new Array(i);
  }

  ngOnInit() {
    this.subscribe();
  }

  ngOnDestroy() {
    this.unSubscribe();
  }

  private load() {
    const year = new Date().getFullYear();
    const month = new Date().getMonth() + 1;
    const start = new Date().getDate();
    const finish = new Date(year, month, 0).getDate();
    this.employeesFactory
      .getPagedByMonthBirthday(month)
      .subscribe(birthdays => {
        if (
          birthdays.items.length !== 0 &&
          birthdays.items[0].birthdayDay - start < 3
        ) {
          this.threeDays = true;
          this.employees = birthdays.items;
          console.log('employees ', this.employees);
        }
      });
    this.loading = false;
  }

  private subscribe() {
    this.subscribeEmployees = this.route.data
      .pipe(map(data => data.company))
      .subscribe(company => {
        this.company = company;
        this.load();
      });
  }

  private unSubscribe() {
    if (this.subscribeEmployees) {
      this.subscribeEmployees.unsubscribe();
    }
  }
}
