import { Component, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { ActivatedRoute } from '@angular/router';
import { map } from 'rxjs/operators';

import { Survey } from 'src/app/services/surveys/survey';
import { Company } from 'src/app/services/companies/company';
import { SharepointService } from 'src/app/services/sharepoint/sharepoint.service';
import { SurveysService } from 'src/app/services/surveys/surveys.service';

@Component({
  selector: 'app-enterprise-polls-informer',
  templateUrl: './enterprise-polls-informer.component.html',
  styleUrls: ['./enterprise-polls-informer.component.scss']
})
export class EnterprisePollsInformerComponent implements OnInit {

  public surveys: Survey[] = null;

  public carouselIsUp: Boolean = false;
  public currentSlide = 0;
  private webId: string = null;
  private company: Company = null;
  private subscription: Subscription = null;
  public loading: Boolean = true;

  constructor(private route: ActivatedRoute,
    private sharepointService: SharepointService,
    private surveysService: SurveysService) { }

  ngOnInit() {
    this.subscription = this.route.data
      .pipe(map(data => data.webId))
      .subscribe(webId => {
        this.webId = webId;
        this.load();
      });
    this.subscription = this.route.data
      .pipe(map(data => data.company))
      .subscribe(company => {
        this.company = company;
      });
  }

  private load() {
    this.surveysService.getItems({ top: 4, orderBy: [['Created', false]], webId: this.webId })
      .subscribe((surveys: Survey[]) => this.surveys = surveys);
      this.loading = false;
  }

  public carouselToFront(val) {
    this.carouselIsUp = val;
  }

  create() {
    window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + `${this.company.url}/Lists/slSurvey/NewForm.aspx?Source=`
      + encodeURIComponent(window.location.href);
  }

  edit(item: any) {
    window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + `${this.company.url}/Lists/slSurvey/EditForm.aspx?ID=${item.id}&Source=`
      + encodeURIComponent(window.location.href);
  }

  remove(item: any) {
    this.surveysService.recycleItem(item, this.webId)
      .subscribe(() => this.load());
  }

  public prepareLink(link: string) {
    return link && link.replace(/\(/gi, '\\(').replace(/\)/gi, '\\)').replace(/'/gi, '%27').replace(/ /gi, '%20')  || null;
  }
}
