import { Injectable } from '@angular/core';
import { ListItemsService } from '../list-items-service';
import { Volunteering } from './volunteering';
import { SharepointService } from '../sharepoint/sharepoint.service';

@Injectable({
  providedIn: 'root'
})
export class VolunteeringService extends ListItemsService<Volunteering> {

  getListTitle(): string {
    return 'Волонтерство';
  }

  getListRelativePath(): string {
    return 'Lists/slVolunteering';
  }

  getSelect(): string {
    return 'Id,Title,slCover,slDescription,Created';
  }

  getExpand(): string {
    return null;
  }

  getOrderBy(): [string, boolean][] {
    return null;
  }

  getTop(): number {
    return 5000;
  }

  getCachingOptions(): import('@pnp/odata').ICachingOptions {
    return null;
  }

  convertListItemToEntity(item: any): Volunteering {
    const entity: Volunteering = {
      id: item.Id,
      title: item.Title,
      cover: item.slCover,
      description: item.slDescription
    };
    return entity;
  }

  convertEntityToListItem(entity: Volunteering) {
    throw new Error('Method not implemented.');
  }

  getMockItems(): Volunteering[] {
    return [];
  }

  isMock(): boolean {
    return false;
  }
}
