import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import * as moment from 'moment';
import { Incident } from 'src/app/services/incidents/incident';
import { Subscription } from 'rxjs';
import { map } from 'rxjs/operators';

export interface WithoutIncidents {
  daysCount: number;
  daysHumanize: string;
}

@Component({
  selector: 'app-incidents-informer',
  templateUrl: './incidents-informer.component.html',
  styleUrls: ['./incidents-informer.component.scss']
})
export class IncidentsInformerComponent implements OnInit, OnDestroy {
  public withoutIncidents: WithoutIncidents = null;
  public incident: Incident = null;
  public modalIsOpen: Boolean = false;
  private incidentSubscribe: Subscription = null;
  public loading: Boolean = true;


  @Input() companyId: number = null;

  constructor(private route: ActivatedRoute) {}

  public toggleModal() {
    this.modalIsOpen = !this.modalIsOpen;
  }

  private subscribe() {
    this.incidentSubscribe = this.route.data
      .pipe(map(data => data.incident))
      .subscribe(incident => {
        this.getIncident(incident);
        this.loading = false;
      });
  }

  private unSubscribe() {
    if (this.incidentSubscribe) {
      this.incidentSubscribe.unsubscribe();
    }
  }

  private getIncident(item: Incident) {
    this.incident = item;
    if (this.incident) {
      const incidentDate = moment(this.incident.date, 'YYYY-MM-DD');
      const currentDate = moment().startOf('day');
      const countDays = moment
        .duration(incidentDate.diff(currentDate))
        .as('days');
      this.withoutIncidents = {
        daysCount: Math.abs(countDays),
        daysHumanize: moment.duration(countDays, 'days').humanize()
      };
    } else {
      this.incident = null;
    }
  }

  ngOnInit() {
    this.subscribe();
  }

  ngOnDestroy() {
    this.unSubscribe();
  }
}
