import { Injectable } from '@angular/core';
import {
  Resolve,
  ActivatedRouteSnapshot,
  RouterStateSnapshot
} from '@angular/router';
import { Observable } from 'rxjs';
import { Incident } from './incident';

import { IncidentsService } from './incidents.service';
import { map, flatMap } from 'rxjs/operators';
import { CompaniesService } from '../companies/companies.service';

@Injectable()
export class IncidentsInformerResolver implements Resolve<Incident> {
  constructor(
    private incidentsService: IncidentsService,
    private companiesService: CompaniesService
  ) {}

  resolve(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Observable<Incident> {
    return this.companiesService.getWebIdByCompanyId(route.params.id).pipe(
      flatMap(id => {
        return this.incidentsService
          .getItems({
            filter: `OData__ModerationStatus eq 0`,
            top: 1,
            webId: id
          })
          .pipe(map((incidents: Incident[]) => incidents[0]));
      })
    );
  }
}
