import { Component, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { ActivatedRoute } from '@angular/router';
import { map } from 'rxjs/operators';

import { News } from 'src/app/services/news/news';
import { NewsService } from 'src/app/services/news/news.service';

@Component({
  selector: 'app-enterprise-news-informer',
  templateUrl: './enterprise-news-informer.component.html',
  styleUrls: ['./enterprise-news-informer.component.scss']
})
export class EnterpriseNewsInformerComponent implements OnInit {
  public webId: string = null;
  public id: number = null;
  private subscription: Subscription = null;
  public news: News[] = null;
  public loading: Boolean = true;

  constructor(
    private route: ActivatedRoute,
    private newsService: NewsService
  ) {}

  public getArray(i: number) {
    return new Array(i);
  }

  ngOnInit() {
    this.id = this.route.snapshot.params.id;
    this.subscription = this.route.data
      .pipe(map(data => data.webId))
      .subscribe(webId => {
        this.webId = webId;
        this.load();
      });
  }

  load() {
    this.newsService
      .getItems({
        filter: `slNewsPublicationDate le datetime'${new Date().toISOString()}' and slNewsRubricLookup/Title ne 'Событие'`,
        top: 5,
        webId: this.webId
      })
      .subscribe(news => {
        this.news = news;
        this.loading = false;
      });
  }
}
