import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs';
import { map } from 'rxjs/operators';

import { Company } from 'src/app/services/companies/company';
import { SharepointService } from 'src/app/services/sharepoint/sharepoint.service';
import { NewsService } from 'src/app/services/news/news.service';
import { News } from 'src/app/services/news/news';
import { NewsRubric } from 'src/app/services/news-rubrics/news-rubric';
import { NewsRubricsService } from 'src/app/services/news-rubrics/news-rubrics.service';

@Component({
  selector: 'app-doings-informer',
  templateUrl: './doings-informer.component.html',
  styleUrls: ['./doings-informer.component.scss']
})
export class DoingsInformerComponent implements OnInit {
  public newsEvents: News[] = [];

  public carouselIsUp: Boolean = false;
  public currentSlide = 0;
  private webId: string = null;
  private company: Company = null;
  private eventRubric: NewsRubric[] = null;
  private subscription: Subscription = null;
  public id: number = null;
  public loading: Boolean = true;

  constructor(
    private route: ActivatedRoute,
    private sharepointService: SharepointService,
    private newsService: NewsService
  ) {}

  ngOnInit() {
    this.id = this.route.snapshot.params.id;
    this.subscription = this.route.data
      .pipe(map(data => data.webId))
      .subscribe(webId => {
        this.webId = webId;
        this.load();
      });
    this.subscription = this.route.data
      .pipe(map(data => data.company))
      .subscribe(company => {
        this.company = company;
      });
  }

  private load() {
    this.eventRubric = this.route.snapshot.data.newsRubrics.filter(
      (r: NewsRubric) => r.title === 'Событие'
    );
    if (this.eventRubric.length > 0) {
          this.newsService
      .getItems({
        filter: `slNewsRubricLookup/Id eq ${this.eventRubric[0].id}`,
        top: 3,
        orderBy: [['Created', false]],
        webId: this.webId
      })
      .subscribe((news: News[]) => {
        this.newsEvents = news;
        this.loading = false;
      });
    }
  }

  public carouselToFront(val) {
    this.carouselIsUp = val;
  }

  create() {
    window.location.href =
      this.sharepointService.getCurrentUserContext().webServerRelativeUrl +
      `${this.company.url}/Lists/slNews/NewForm.aspx?Source=` +
      encodeURIComponent(window.location.href);
  }

  edit(item: any) {
    window.location.href =
      this.sharepointService.getCurrentUserContext().webServerRelativeUrl +
      `${this.company.url}/Lists/slNews/EditForm.aspx?ID=${item.id}&Source=` +
      encodeURIComponent(window.location.href);
  }

  remove(item: any) {
    this.newsService.recycleItem(item, this.webId).subscribe(() => this.load());
  }

  public prepareLink(link: string) {
    return (
      (link &&
        link
          .replace(/\(/gi, '\\(')
          .replace(/\)/gi, '\\)')
          .replace(/'/gi, '%27')
          .replace(/ /gi, '%20')) ||
      null
    );
  }
}
