import { Injectable } from '@angular/core';
import { Fame } from './fame';
import { ListItemsService } from '../list-items-service';

@Injectable({
  providedIn: 'root'
})
export class FamesService extends ListItemsService<Fame>  {

  getListTitle(): string {
    return 'Доска почета';
  }

  getListRelativePath(): string {
    return 'Lists/slFames';
  }

  getSelect(): string {
    return 'Id,Title,slPosition,slEmployee/Id,slEmployee/Title,slEmployee/Name,slEmployee,Created';
  }

  getExpand(): string {
    return 'slEmployee';
  }

  getOrderBy(): [string, boolean][] {
    return [['Created', false]];
  }

  getTop(): number {
    return 5000;
  }

  getCachingOptions(): import('@pnp/odata').ICachingOptions {
    return null;
  }

  private getLogin(name: string): string {
    let login = name;
    if (login && login.indexOf('|') >= 0) {
      login = login.substring(login.lastIndexOf('|') + 1);
    }
    return login;
  }

  convertListItemToEntity(item: any): Fame {
    const comment: Fame = {
      id: item.Id,
      title: item.Title,
      position: item.slPosition,
      employeeId: item.slEmployee.Id,
      employeeTitle: item.slEmployee.Title,
      employeePhoto: `/_layouts/15/userphoto.aspx?size=M&username=${this.getLogin(item.slEmployee.Name)}`,
      employeeRoute: ['/phonebook', item.Id],
      employeeLogin: this.getLogin(item.slEmployee.Name)
    };
    return comment;
  }

  convertEntityToListItem(entity: Fame) {
    throw new Error('Method not implemented.');
  }

  getMockItems(): Fame[] {
    return [];
  }

  isMock(): boolean {
    return false;
  }
}
